div.container {
    width: 100%;
    margin: auto;
    text-align: center;
}

h1 {
    margin-top: 50px;
    color: white;
    font-size: 4rem;
}

hr {
    width: 40%;
    margin: 50px auto;
    border: .8px solid white;
    border-radius: 1px;
}

.form-add {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}

input.add-todo {
    font-family: monospace;
    outline: none;
    border: none;
    height: 50px;
    width: 40%;
    padding: 10px 20px;
}

.btn-addTodo {
    padding: 10px 20px;
    border: none;
    background: darkcyan;
    color: white;
    transition: all .3s;
    cursor: pointer;
    font-size: 1.1rem;
    height: 50px;
}

