.todo {
    width: 0%;
    background-color: rgba(255, 255, 255, .7);
    color: black;
    width: 60%;
    margin: 5px auto 0px auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 5px 10px;
    font-size: 1.5rem;
    border-radius: 15px;
}

.todo:first-child {
    margin-top: 30px;
}

.todo p{
    width: 100%;
    overflow: hidden;
    font-weight: 600;
    padding-top: 5px;
}

.todo-action {
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.todo-text-info {
    display: flex;
    justify-content: center;
    align-items: center;
    overflow-x: hidden;
}

.todo-line {
    width: 5px;
    height: 5vh;
    margin: 0px 15px 0px 5px;
    border-radius: 10px;
}

.text-overline {
    text-decoration: line-through;
    color: rgb(58, 56, 56);
    font-weight: 200;
}

.todosList {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}